.card h3 {
  display: flex;
  align-items: center;
  justify-content: center;
}

/* =======  CARDS ======= */
.card {
  padding: 1.5rem 2rem;
  border-radius: 0.25rem;
  margin-bottom: 2rem;
  background-color: #fff;
  color: var(--dark-blue);
}
.card.total {
  background-color: #49AA26;
  color: white;
}
.card h3 {
  font-weight: normal;
  font-size: 1rem;
  justify-content: space-between;
}
.card p {
  font-size: 2rem;
  line-height: 3rem;
  margin-top: 1rem;
}